@media (max-width: 767px) {
  .error_message {
    position: absolute;

    font-size: 12px;
    line-height: 14px;
    padding: 0;
    margin: 0;
    display: flex;
    align-items: center;
    color: #da1414;
    top: 72px;
    left: 26px;
    z-index: 10;
  }

  .success_message {
    position: absolute;

    font-size: 12px;
    line-height: 14px;
    padding: 0;
    margin: 0;
    display: flex;
    align-items: center;
    color: green;
    top: 72px;
    left: 26px;
    z-index: 10;
  }
  .main_container {
    display: flex;
    width: 100%;
    height: 1024px;
    background: #dcebf7;

    justify-content: center;
    align-items: center;
  }

  .inside_container {
    background: #fff;
    border-radius: 8px;
    width: 375px;
    height: 376px;
    padding: 0 24px;
    margin: 0 20px;
  }

  .title {
    margin-top: 40px;

    margin-bottom: 32px;

    font-weight: 600;
    font-size: 24px;
    line-height: 100%;

    color: var(--nav-button-color-hover);

    text-shadow: 0px 47px 355px rgba(0, 0, 0, 0.07),
      0px 9.4px 57.6875px rgba(0, 0, 0, 0.035);
  }

  .email_box {
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    padding: 0px;
    gap: 8px;
    isolation: isolate;
    margin-bottom: 24px;
  }

  .password_box {
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    padding: 0px;
    gap: 8px;
    isolation: isolate;
  }

  .label_email {
    width: 32px;
    height: 15px;

    font-weight: 600;
    font-size: 12px;
    line-height: 15px;

    color: var(--primary-text-color);

    flex: none;
    order: 0;
    flex-grow: 0;
    z-index: 0;
  }
  .label_email.error_label {
    color: red;
  }

  .label_email.success_label {
    color: green;
  }

  .field {
    box-sizing: border-box;

    width: 100%;
    height: 46px;

    background: #ffffff;
    border: 1px solid rgba(220, 227, 229, 0.6);
    border-radius: 8px;

    flex: none;
    order: 1;
    flex-grow: 0;
    z-index: 1;

    font-size: 14px;
    line-height: 129%;

    color: var(--primary-text-color);

    padding-left: 14px;

    outline-color: black;
  }

  .field.error {
    border: 1px solid red;
  }

  .field.success {
    border: 1px solid green;
  }

  .button {
    cursor: pointer;
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 10px;

    border-style: none;
    margin-top: 10px;

    width: 100%;
    height: 46px;

    background: var(--nav-button-color-hover);
    box-shadow: 4px 2px 16px rgba(136, 165, 191, 0.48);
    border-radius: 16px;

    font-weight: 600;
    font-size: 14px;
    line-height: 129%;

    letter-spacing: -0.02em;

    color: var(--sidebar-background-color);
  }

  .button:hover {
    background: var(--button-color-hover);
  }
    .button_google {
      cursor: pointer;
      display: flex;
      justify-content: center;
      align-items: center;
      gap: 10px;
  
      border-style: none;
      margin-top: 10px;
  
      width: 100%;
      height: 46px;
  
      background: #fff;
      box-shadow: 4px 2px 16px rgba(136, 165, 191, 0.48);
      border-radius: 16px;
      border: 1px solid #2b78ef;
  
      font-weight: 600;
      font-size: 14px;
      line-height: 129%;
  
      letter-spacing: -0.02em;
  
      color: #111;
    }
  
    .button_google:hover,
    .button_google:focus {
    color: #fff;
      background-color: #2b78ef;
      transition: all 250ms cubic-bezier(0.4, 0, 0.2, 1);
    }
  .logIn {
    width: 20px;
    height: 20px;
  }

  .sign {
    position: absolute;
    font-weight: 600;
    color: var(--nav-button-color-hover);
    font-size: 14px;
    line-height: 129%;

    top: 740px;
    text-decoration: underline;
  }
  .iconOk {
    position: absolute;
    left: 80%;
    top: 36px;
    z-index: 10;
    fill: green;
  }

  .iconError {
    position: absolute;
    left: 80%;
    top: 36px;
    z-index: 10;
    fill: #da1414;
  }
}

